//FOLDS
.folds {
    opacity: 0.3;
    animation: alternate folds 3s 1; //infinite
    -webkit-animation: alternate folds 3s 1; //infinite
    .folds-reverse {
        opacity: 0.3;
        animation: reverse folds 3s 1; //infinite
        -webkit-animation: reverse folds 3s 1; //infinite
    }
}
//FOLDS END

//FLOP TURN RIVER
.flip-card.action-flip {
    .flip-card-inner {
        -webkit-animation: ease card-center-spinning 0.5s 1; //infinite
        animation: ease card-center-spinning 0.5s 1; //infinite
        transform: scale(1) rotateY(180deg);
        //opacity: 1;
    }
    &.action-flip-reverse .flip-card-inner {
        -webkit-animation: reverse card-center-spinning-reverse 0.5s 1; //infinite
        animation: reverse card-center-spinning-reverse 0.5s 1; //infinite
        transform: scale(0) rotateY(0deg);
        //opacity: 0;
    }
}
//FLOP TURN RIVER END

//AREA ACTION
#area-1 {
    &.calls-fade {
        -webkit-animation: ease chips-p1 0.5s 1; //infinite
        animation: ease chips-p1 0.5s 1; //infinite
        opacity: 1;
        &.calls-fade-reverse {
            -webkit-animation: reverse chips-p1-reverse 0.5s 1; //infinite
            animation: reverse chips-p1-reverse 0.5s 1; //infinite
            opacity: 0;
        }
    }
}

