//HOLE CARDS
.hh-player {
    .card {
        position: absolute;
        display: inline-block;
        top: -50vh;
        left: -30vw;
        opacity: 0;
    }
    #position-1 {
        &.hole-card {
            .card {
                -webkit-animation: alternate hole-cards-player-1 3s 1; //infinite
                animation: alternate hole-cards-player-1 3s 1; //infinite
                backface-visibility: hidden;
                transform: translate3d(755px, 595px, 0px) perspective(600px) scale(1, 1) rotateX(00deg) rotateZ(-0deg)
                    rotateY(-4deg) rotate(0deg);
                backface-visibility: hidden;
                z-index: 164;
                transition: all 0.3s ease-in-out 0s;
                opacity: 1;
            }
            &.hole-card-reverse .card {
                -webkit-animation: reverse hole-cards-reverse-player-1 0.5s 1; //infinite
                animation: reverse hole-cards-reverse-player-1 0.5s 1; //infinite
                z-index: 164;
                opacity: 0;
            }
        }
    }
}

