//PLAYER ACTION CALLS/FOLD/CHECKS
.player-action {
    &.action-show {
        opacity: 1;
        animation: alternate player-action 0.5s 1; //infinite
        &.action-show-reverse {
            opacity: 0;
            animation: reverse player-action-reverse 0.5s 1; //infinite
        }
    }
}
//PLAYER ACTION CALLS/FOLD/CHECKS END
